.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #ff4444;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;

  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.header h1 {
  margin-left: 2rem;
  width: 69%;
}

.header h1 a {
  text-decoration: none;
  color: white;
}
